
import React from 'react'
import { Link } from 'react-router-dom'
function PrivacyPolicy() {
	return (
		<>
			<div className='doc__wrapper'>
				<p>Політика конфіденційності</p>
				<br />
				<p>Це Положення про конфіденційність і захист персональних даних (далі - Положення) є додатком до&nbsp;<Link to={'/docs/UserAgreement'}>Корстувацької угоди</Link> &nbsp;між фізичними особами (Користувачами і Фахівцями), які реєструються на Сайті, і Підприємцем.</p>
				<p>Усі терміни, вживані в Положенні, використовуються в значеннях, встановлених Користувацькою угодою, включаючи додаток до неї.</p>
				<p>Положення діє відносно усієї інформації, яку Підприємець може отримати про Користувачів і Фахівців при використанні ними будь-якого з сервісів, служб, програм і продуктів Сайту (далі - Сервіси, Сервіси Сайту). Використання Сервісів Сайту означає беззастережну згоду Користувача/Фахівця з Положенням.</p>
				<p>У разі незгоди з Положенням Користувач/Фахівець повинен утриматися від користування Сайтом.</p>
				<p><br /></p>
				<p>1. Персональні дані Користувачів і Фахівців</p>
				<p>1.1. Відповідно до законодавства України, під персональними даними слід розуміти відомості або сукупність відомостей про фізичну особу, яку ідентифіковано або може бути конкретно ідентифіковано. У рамках цього Положення під &quot;персональними даними користувача&quot; слід розуміти наступне:&nbsp;</p>
				<p>1.1.1. Персональні дані, які Користувач/Фахівець надає про себе під час Реєстрації або в процесі використання Сервісів. Обов&apos;язкові для надання Сервісів дані позначаються спеціальним чином. Інша інформація надається Користувачем/Фахівцем з його розсуду і її надання не є обов&apos;язковим для Реєстрації або користування Сервісами.</p>
				<p>1.1.2. Дані, які автоматично передаються в процесі їх використання за допомогою встановленого на пристрої Користувача програмного забезпечення, у тому числі IP- адреса, інформація з cookie, інформація про браузер користувача (чи про іншу програму, за допомогою якої здійснюється доступ до Сервісів), час доступу, адреса прошеної сторінки.</p>
				<p>1.1.3. Інша інформація про користувача, збір і/або надання якої визначено в документах, що регламентують діяльність окремих Сервісів Сайту.</p>
				<p>1.2. Це Положення застосовується тільки до Сервісів Сайту. Підприємець не контролює і не несе відповідальність за зміст інших сайтів в мережі Інтернет, на які Користувач/Фахівець може перейти по посиланнях, доступних на Сайті, зокрема в Контенті.</p>
				<p>1.3. Підприємець за загальним правилом не перевіряє достовірності персональних даних, наданих Користувачами/Фахівцями. Підприємець презюмирует сумлінність Користувачів/Фахівців і виходить з того, що вони надають достовірну і достатню персональну інформацію з питань, пропонованих під час Реєстрації і підтримують цю інформацію в актуальному стані.</p>
				<p><br /></p>
				<p>2. Обробка персональних даних</p>
				<p>2.1. Сайт збирає і зберігає тільки ті персональні дані, які потрібні для надання Сервісів і надання послуг (виконання угод і договорів з Користувачем/Фахівцем).</p>
				<p><br /></p>
				<p>2.2. Персональну інформацію користувача Сайт може використати в наступних цілях:</p>
				<p>2.2.1. Ідентифікація сторони у рамках угод і договорів з Підприємцем;</p>
				<p>2.2.2. Надання Сервісів, що персоналізуються;</p>
				<p>2.2.3. Зв&apos;язок з Користувачем/Фахівцем, у тому числі напрям повідомлень (у тому числі комерційних), запитів і інформації, що стосуються використання Сервісів, надання послуг, а також обробки запитів і заявок;</p>
				<p>&nbsp;2.2.4. Поліпшення якості сервісу, зручності його використання, розробка нових сервісів і послуг;</p>
				<p>2.2.5. Таргетування рекламних матеріалів;</p>
				<p>2.2.6. Проведення статистичних і інших досліджень на основі знеособлених даних.</p>
				<p>3. Згода суб&apos;єкта персональних даних. Умови обробки персональних даних і їх передачі третім особам</p>
				<p>3.1. Сайт зберігає персональну інформацію Користувачів відповідно до сучасних технічних норм і вимог.</p>
				<p>3.2. Обробка персональних даних здійснюється виключно за умови надання Користувачем/Фахівцем однозначної згоди на обробку таких даних.</p>
				<p><br /></p>
				<p>Згода Користувача/Фахівця на обробку персональних даних надається при Реєстрації шляхом проставлення відмітки про надання дозволу на обробку персональних даних, в цілях обробки, сформульованої в Положенні, безякого реєстрація в системі не можлива.</p>
				<p>Технічні засоби Сайту не створюють можливості обробки персональних даних до моменту проставлення такої відмітки.</p>
				<p>3.3. На персональні дані Користувачів і Фахівців поширюється правовий режим інформації з обмеженим доступом - конфіденційній інформації, крім випадків добровільного поширення Користувачем/Фахівцем такої інформації для загального доступу необмеженого кола осіб.</p>
				<p>3.4. Сайт має право передати персональну інформацію користувача третім особам в наступних випадках:</p>
				<p>3.4.1. Користувач/Фахівець дав згоду на здійснення таких дій;</p>
				<p>3.4.2. Така передача потрібна у рамках використання певного Сервісу або для надання послуги;</p>
				<p>3.4.3. Передача передбачена нормами законодавства;</p>
				<p>3.4.4. У зв&apos;язку із заміною сторони Підприємця в зобов&apos;язанні за Користувацькою угодою;</p>
				<p>3.4.5. З метою забезпечення можливості захисту прав і законних інтересів Підприємця або третіх осіб у випадках, коли Користувач порушує Користувацьку угоду.</p>
				<p><br /></p>
				<p>3.5. При обробці персональних даних Користувачів Підприємець керується нормами Закону України &quot;Про захист персональних даних&quot;.&nbsp;</p>
				<p>4. Зміна користувачем персональної інформації</p>
				<p>Користувач/Фахівець може у будь-який момент змінити (оновити, доповнити) надані їм персональні дані або їх частину, скориставшись функцією редагування персональних даних в розділах &quot;Мій аккаунт&quot; і &quot;Мій профіль&quot; облікового запису Користувача/Фахівця, доступного після Авторизації на Сайті.</p>
				<p>5. Інші права Користувача</p>
				<p>5.1. Підприємець гарантує забезпечення прав Користувача/Фахівця, передбачених нормами частини другої статті 8 Закону України &quot;Про захист персональних даних&quot;, а саме:</p>
				<p>5.1.1. Знати про джерела збору, місцезнаходження своїх персональних даних, мети їх обробки, місцезнаходження або місце проживання (перебування) власника або розпорядника персональних даних або дати відповідне доручення про отримання цієї інформації уповноваженим їм особам, окрім випадків, встановлених законом;</p>
				<p>5.1.2. Отримувати інформацію про умови надання доступу до персональних даних, включаючи інформацію про третіх осіб, яким передаються його персональні дані;</p>
				<p>5.1.3. На доступ до своїх персональних даних;</p>
				<p><br /></p>
				<p>5.1.4. Отримувати не пізніше чим за тридцять календарних днів з дня вступу запиту, окрім випадків, передбачених законом, відповідь про те, чи обробляються його персональні дані, а також отримувати вміст таких персональних даних;</p>
				<p>5.1.5. Пред&apos;являти мотивовану вимогу власникові персональних даних із запереченням проти обробки своїх персональних даних;</p>
				<p>5.1.6. Пред&apos;являти вмотивовану вимогу про зміну або знищення своїх персональних даних будь-яким власником і розпорядником персональних даних, якщо ці дані обробляються незаконно або є недостовірними;</p>
				<p>5.1.7. На захист своїх персональних даних від незаконної обробки і випадкової втрати, знищення, пошкодження у зв&apos;язку з умисним захованням, ненаданням або невчасним їх наданням, а також на захист від надання відомостей, які є недостовірними або такими, що порочать честь, гідність і ділову репутацію фізичної особи;&nbsp;</p>
				<p>5.1.8. Звертатися із скаргами на обробку своїх персональних даних до Уповноваженого з прав людини або до суду;</p>
				<p>5.1.9. Застосовувати засоби правового захисту в разі порушення законодавства про захист персональних даних;&nbsp;</p>
				<p>5.1.10. Вносити обмовки про обмеження права на обробку своїх персональних даних при наданні згоди;&nbsp;</p>
				<p>5.1.11. Відгукнути згоду на обробку персональних даних;</p>
				<p>5.1.12. Знати механізм автоматичної обробки персональних даних;&nbsp;</p>
				<p>5.1.13. На захист від автоматизованого рішення, яке має для нього правові наслідки.</p>
				<p>6. Заходи, що приймаються для захисту персональних даних</p>
				<p>Адміністрація Сайту робить необхідні і достатні організаційні і технічні заходи по захисту персональних даних Користувача/Фахівця від неправомірного або випадкового доступу, знищення, зміни, блокування, копіювання, поширення, а також від інших неправомірних дій третіх осіб.</p>
				<p>7. Зміна Положення</p>
				<p>7.1. Підприємець має право вносити зміни до цього Положення. Нова редакція Положення набирає чинності з моменту її розміщення, якщо інше не передбачене новою редакцією Положення. &nbsp;&nbsp;</p>
				<p>7.2. До стосунків між Користувачем/Фахівцем і Підприємцем, що виникає у зв&apos;язку з вживанням Положення, підлягає вживанню право України.</p>
				<p>8. Зворотний зв&apos;язок. Питання і пропозиції</p>
				<p>Всі пропозиції або питання з приводу цього Положення слід повідомляти по електронній пошті: centrmed@gmail.com</p>
				<p><br /></p>
				<p><br /></p>

			</div></>
	)
}

export default PrivacyPolicy